<template>
	<view class="container">
		<view class="list-cell b-b m-t" hover-class="cell-hover" :hover-stay-time="50" @click="select_img">
			<text class="cell-tit">头像</text>
			<image class="header_img" :src="test_img" mode="aspectFill"></image>
			<!-- <text class="cell-more yticon icon-you"></text> -->
		</view>
		<view class="list-cell b-b m-t" hover-class="cell-hover" :hover-stay-time="50">
			<text class="cell-tit">昵称</text>
			<input class="uni-input" placeholder="请输入昵称" maxlength="8" v-model="name"/>
			<!-- <text class="cell-more yticon icon-you"></text> -->
		</view>
		<view class="list-cell b-b m-t" hover-class="cell-hover" :hover-stay-time="50">
			<text class="cell-tit">个性签名</text>
			<input class="uni-input" placeholder="请输入签名" maxlength="15" v-model="qianming"/>
		</view>
		<view class="list-cell b-b m-t" hover-class="cell-hover" :hover-stay-time="50">
			<text class="cell-tit">性别</text>
			<view class="uni-padding-wrap">
				<view>
					<radio-group @change="radioChange">
					   <label class="radio"><radio value="1" />男</label>
					   <label class="radio"><radio value="0" />女</label>
					</radio-group>
				</view>
			</view>
		</view>
		<view class="list-cell b-b m-t" hover-class="cell-hover" :hover-stay-time="50" @click="select_time">
			<text class="cell-tit">生日</text>
			<text class="cell-content">{{birthday ? birthday : '点击设置生日'}}</text>
		</view>
		<min-popup heightSize="500" :show="show" @close='close'>
		    <min-picker 
		    :endTime="endTime"
		    :startTime="startTimes"
		    @cancel="cancel"
		    @sure="sure"
		    >
		    </min-picker>
		</min-popup>
	</view>
</template>

<script>
	import MinPopup from '../components/min-picker/min-popup.vue'
	import MinPicker from '../components/min-picker/min-picker.vue'
	import test_img from '../../static/img4.jpg'
	import moment from 'moment'
	export default {
		data() {
			return {
				user_info: '',
				name: '我的名字是八个字',
				test_img: test_img,
				qianming: '',
				startTimes:[1970,1,1],
				endTime:  Number(moment().format('YYYY')) - 18,
				show: false,
				birthday: '',
				six: 0
			}
		},
		components: {
			MinPopup, MinPicker
		},
		onLoad() {
			this.$http
			.post('/api/getuser', 
			{
				userid: uni.getStorageSync('userId')
			}, {})
			.then(res => {
				this.user_info = res.data
			})
			.catch(err => {
			})
		},
		methods: {
			select_img () {
				
			},
			radioChange (e) {
				this.six = Number(e.target.value)
				console.log(this.six)
			},
			select_time () {
				this.show = true
			},
			// 取消事件
			cancel(){
				this.close()
			},
			// 确认事件
			sure(e){
				this.birthday = e
			},
			// 关闭picker
			close(){
				this.show = false
			}
		}
	}
</script>

